          />
        </div>
        <div class="title-botton" @click="toggleStatus('step')">
          <img
            v-show="buttonMun.step"
            src="../../../assets/img/step_click.png"
          />
          <img
            class="default"
            v-show="!buttonMun.step"
            src="../../../assets/img/step_default.png"
          />
        </div>
        <div class="title-botton" @click="toggleStatus('pause')">
          <img
            v-show="buttonMun.pause"
            src="../../../assets/img/pause_click.png"
          />
          <img
            class="default"
            v-show="!buttonMun.pause"
            src="../../../assets/img/pause_default.png"
          />
        </div>
        <div class="title-botton" @click="toggleStatus('stop')">
          <img
            v-show="buttonMun.stop"
            src="../../../assets/img/stop_click.png"
          />
          <img
            class="default"
            v-show="!buttonMun.stop"
            src="../../../assets/img/stop_default.png"
          />
        </div> -->
      </el-col>
      <el-col class="content" :span="24">
        <table class="content-table">
          <!--  -->
          <thead>
            <tr>
              <th rowspan="3"></th>
              <th colspan="2" v-for="(item, index) in listTitles" :key="index">
                {{ index + 1 }}
              </th>
              <!-- <th colspan="2">2</th>
              <th colspan="2">3</th>
              <th colspan="2">4</th>
              <th colspan="2">5</th>
              <th colspan="2">6</th> -->
            </tr>
            <tr>
              <th colspan="2" v-for="(item, index) in listTitles" :key="index">
                {{ showData.length > 0 ? (showData[0][item] || '--') : '--' }}
              </th>
              <!-- <th colspan="2">000002</th>
              <th colspan="2">000003</th>
              <th colspan="2">000004</th>
              <th colspan="2">000005</th>
              <th colspan="2">000006</th> -->
            </tr>
            <tr>
              <th v-for="(item, index) in listTitles.length * 2" :key="index">
                {{ index % 2 == 0 ? "" : "" }}
              </th>
              <!-- <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th> -->
            </tr>
          </thead>
          <!--  v-for  -->
          <tbody v-loading="tableLoad" element-loading-text="" element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.6)">
            <tr v-for="(item, index) in showData" :key="index">
              <td class="project">{{ item.itemName }}</td>
              <td :class="item['1b'] == '' ? 'unqualified' : ''">
                {{ item["1b"] || '--' }}
              </td>
              <td :class="item['1j'] == '' ? 'unqualified' : ''">
                {{ item["1j"] || '--' }}
              </td>
              <td :class="item['2b'] == '' ? 'unqualified' : ''">
                {{ item["2b"] || '--' }}
              </td>
              <td :class="item['2j'] == '' ? 'unqualified' : ''">
                {{ item["2j"] || '--' }}
              </td>
              <td :class="item['3b'] == '' ? 'unqualified' : ''">
                {{ item["3b"] || '--' }}
              </td>
              <td :class="item['3j'] == '' ? 'unqualified' : ''">
                {{ item["3j"] || '--' }}
              </td>
              <td :class="item['4b'] == '' ? 'unqualified' : ''">
                {{ item["4b"] || '--' }}
              </td>
              <td :class="item['4j'] == '' ? 'unqualified' : ''">
                {{ item["4j"] || '--' }}
              </td>
              <td :class="item['5b'] == '' ? 'unqualified' : ''">
                {{ item["5b"] || '--' }}
              </td>
              <td :class="item['5j'] == '' ? 'unqualified' : ''">
                {{ item["5j"] || '--' }}
              </td>
              <td :class="item['6b'] == '' ? 'unqualified' : ''">
                {{ item["6b"] || '--' }}
              </td>
              <td :class="item['6j'] == '' ? 'unqualified' : ''">
                {{ item["6j"] || '--' }}
              </td>
            </tr>
          </tbody>
        </table>
      </el-col>
      <el-col class="footer" :span="24">
        <button class="back-page" @click="goBack()"></button>
        <button class="first-end-page-button" @click="toFirstPage()">
          
        </button>
        <button class="page-button" @click="() => (currentPage > 1 ? currentPage-- : '')">
          
        </button>

        <el-pagination class="page-list" :page-size="19" layout="pager" :current-page="currentPage" :total="total"
          @current-change="handleCurrentChange">
        </el-pagination>
        <button class="page-button" @click="() => (currentPage < pages ? currentPage++ : '')">
          
        </button>
        <button class="first-end-page-button" @click="toLastPage()">
          
        </button>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { transformerData } from "../virtualData.js";

export default {
  data() {
    return {
      // buttonMun: {
      //   start: false,
      //   step: false,
      //   pause: false,
      //   stop: true,
      // },
      //
      listTitles: [
        "1_meterNo",
        "2_meterNo",
        "3_meterNo",
        "4_meterNo",
        "5_meterNo",
        "6_meterNo",
      ],
      interval: null,
      //19
      initData: [],
      showData: [],
      tableLoad: false,
      total: 1001,
      currentPage: 1,
    };
  },
  computed: {
    pages() {
      return Math.ceil(this.total / 19);
    },
  },
  watch: {
    currentPage(newValue, oldValue) {
      this.tableLoad = true;
      this.showData = this.initData.slice(
        19 * (newValue - 1),
        19 * (newValue - 1) + 19
      );
      this.tableLoad = false;
    },
  },
  mounted() {
    this.init();
    this.interval = setInterval(() => {
      this.init();
    }, 10000);
  },
  methods: {
    //
    init() {
      this.tableLoad = true;
      //
      this.$request(
        "post",
        "/bigScreen/processMonitor",
        {
          equipNo: this.$route.query.equipNo,
          taskNo: this.$route.query.taskNo,
          equipType: this.$route.query.equipType,
        },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
        .then((res) => {
          this.initData = res.data;
          this.total = this.initData.length;
          if (this.currentPage !== 1) {
            this.showData = this.initData.slice(
              19 * (this.currentPage - 1),
              19 * (this.currentPage - 1) + 19
            );
          } else {
            this.showData = this.initData.slice(0, 19);
          }
        })
        .finally(() => {
          this.tableLoad = false;
        });
      // this.initData = transformerData;
      // this.total = transformerData.length;
      // this.showData = this.initData.slice(0, 19);
      // this.tableLoad = false;
    },
    //
    // toggleStatus(bt) {
    //   // console.log(bt);
    //   for (const key in this.buttonMun) {
    //     this.buttonMun[key] = key == bt ? true : false;
    //   }
    // },
    //
    goBack() {
      this.$router.go(-1);
    },
    //
    toFirstPage() {
      this.currentPage = this.currentPage = 1;
    },
    //
    toLastPage() {
      this.currentPage = this.pages;
    },
    //
    handleCurrentChange(val) {
      this.currentPage = val;
    },
  },
  beforeDestroy() {
    clearInterval(this.interval);
  }
};
</script>
<style scoped>
