<template>
  <div class="indexBox" @mousemove="move">
    <vue-particles v-if="isShow" color="#00C6FB" :particleOpacity="0.8" shapeType="circle" :particleSize="40"
      :lineLinked="false" :moveSpeed="3" :hoverEffect="false" :clickEffect="false" class="particles">
    </vue-particles>
    <el-row>
      <el-col :span="6" style=" height: 77rem; padding: 1rem 0 1rem 1rem">
        <el-row>
          <el-col :span="24">
            <device-inform :deviceList="this.deviceList" />
          </el-col>
          <el-col :span="24">
            <tester :testerList="this.testerList" />
          </el-col>
          <el-col :span="24">
            <env-watch :envList="this.envList" />
          </el-col>
          <el-col :span="24">
            <real-time-data :realTimeList="this.realTimeList" />
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="18">
        <component :is="showComponent" @statusChange="statusChange"></component>
        <!-- <electric-meter-watch v-if="this.$route.query.equipType == ''" />
        <normal-meter-watch v-if="this.$route.query.equipType == ''" />
        <transformer-watch v-if="this.$route.query.equipType == ''" /> -->
      </el-col>
    </el-row>
  </div>
</template>

<script>
import deviceInform from "./components/deviceInform.vue";
import tester from "./components/tester.vue";
import envWatch from "./components/envWatch.vue";
import realTimeData from "./components/realTimeData.vue";
import electricMeterWatch from "./components/electricMeterWatch.vue";
import normalMeterWatch from "./components/normalMeterWatch.vue";
import transformerWatch from "./components/transformerWatch.vue";
import { electricMeterData } from "./virtualData.js";

import { mapState, mapActions, } from 'pinia';
import useDeviceStore from '../../store/index.js';

const store = useDeviceStore();

export default {
  components: {
    deviceInform,
    tester,
    envWatch,
    realTimeData,
    electricMeterWatch,
    normalMeterWatch,
    transformerWatch,
  }, //
  name: "facilities",
  data() {
    return {
      isShow: false,
      showComponent: '',
      processWatchList: {
        '': 'electricMeterWatch',
        '': 'normalMeterWatch',
        '': 'transformerWatch',
      },
      deviceList: [
        // { name: "", value: "" },
        // { name: "", value: "02031" },
        // { name: "", value: "" },
        // { name: "", value: "2308" },
        // { name: "", value: "0.05" },
        // { name: "", value: "20220513" },
        // { name: "", value: "20240512" },
        // { name: "", value: "" },
      ],
      testerList: [],
      envList: {},
      realTimeList: {},
      // watchList: [],
      interval: null,
      intervalSum: 0,
      hideTime: 1,
      tempObj: {},
    };
  },
  mounted() {
    this.init();
    this.interval = setInterval(() => {
      if (this.intervalSum > 100) {
        location.reload();
      }
      this.init();
      this.intervalSum++;
    }, 10000);
  },
  methods: {
    init() {
      // console.log('route', this.$route.query);
      //
      this.$request("get", "/bigScreen/durationLock").then((res) => {
        this.hideTime = res.data;
        if (this.hideTime) {
          this.updateHide();
        }
      });
      this.showComponent = this.processWatchList[this.$route.query.equipType]
      //API
      this.$request(
        "post", "/bigScreen/getEquipInfoByNo", {
        "equipNo": this.$route.query.equipNo
      }
      ).then(res => {
        // const res = {
        //   "msg": "",
        //   "code": 0,
        //   "data":  {
        //     "createBy": null,
        //     "createTime": null,
        //     "updateBy": null,
        //     "updateTime": null,
        //     "remark": null,
        //     "id": 7,
        //     "equipNo": "2308##003##KL001",
        //     "equipName": "",
        //     "laboratoryNo": "2308",
        //     "manufacturers": "",
        //     "model": "CL3000D",
        //     "isDetail": "",
        //     "checkTime": "2023-12-20",
        //     "validDate": "2024-12-31",
        //     "equipType": "",
        //     "equipDj": "0.05"
        // }
        // }
        this.deviceList.length = 0
        this.deviceList.push({ name: "", value: res.data.equipName })
        this.deviceList.push({ name: "", value: res.data.equipNo })
        this.deviceList.push({ name: "", value: res.data.manufacturers })
        this.deviceList.push({ name: "", value: res.data.laboratoryNo })
        this.deviceList.push({ name: "", value: res.data.equipDj })
        this.deviceList.push({ name: "", value: res.data.checkTime })
        this.deviceList.push({ name: "", value: res.data.validDate })
        this.deviceList.push({ name: "", value: res.data.isDetail })
        // console.log(':', store.status);
      }).finally(() => {
        store.updateStatus(this.deviceList[this.deviceList.length - 1].value);
      })
      //
      this.$request(
        "post", "/bigScreen/personnelInfo", {
        "equipNo": this.$route.query.equipNo
      }
      ).then(res => {
        this.testerList = res.data
        if (this.testerList.length == 0) {
          this.testerList = [
            {
              personnelName: "",
              post: "",
            },
          ]
        }
      })
      //
      this.$request(
        "post", "/bigScreen/onlineLab", {
        labNo: window.localStorage.getItem('labNo'),
      }, {
        headers: {
          "Content-Type": "application/json",
        },
      }
      ).then((res) => {
        this.envList = res.data.sensorInfo;
        if (!res.data.sensorInfo) {
          this.envList = {
            temperature: "",
            temperatureState: "",
            humidity: "",
            humidityState: "",
            illumination: "",
            illuminationState: "",
            electromagnetism: "",
            electromagnetismState: "",
          }
        }
      });
      //
      this.$request(
        "post", "/bigScreen/equipDataInfo", {
        equipNo: this.$route.query.equipNo
      }, {
        headers: {
          "Content-Type": "application/json",
        },
      }
      ).then((res) => {
        this.realTimeList = res.data[0];
        // this.realTimeList = {
        //   "equipName": "",
        //   "phia": "0.00",
        //   "phib": "240.00",
        //   "phic": "120.00",
        //   "checker": "",
        //   "Ua": "99.99",
        //   "Ub": "100.01",
        //   "Uc": "99.10",
        //   "La": "5.01",
        //   "equipType": "",
        //   "Lb": "4.99",
        //   "Lc": "4.98",
        //   "equipNo": "2303##001##HP001",
        //   "projectName": "Q+  0.8C 1.0Ib "
        // }

        //   {
        //   phase: "123",
        //   equipName: "",
        //   lightLoad: "123",
        //   rating: "123",
        //   accuracy: "123",
        //   oneCurrent: "123",
        //   updateTime: 1702650566000,
        //   checker: "",
        //   params: {},
        //   booster: "123",
        //   twoCurrent: "123",
        //   equipType: "",
        //   taskNo: "1202002",
        //   equipNo: "2304##002##WSD001",
        //   id: 1,
        //   resistor: "123",
        //   projectName: "Q+  0.8C 1.0Ib ",
        //   powerFactor: "123",
        // }
      });
      //
      // this.$request(
      //   "post",
      //   "/bigScreen/processMonitor",
      //   {
      //     equipNo: this.$route.query.equipNo,
      //     taskNo: this.$route.query.taskNo,
      //     equipType: this.$route.query.equipType,
      //   },
      //   {
      //     headers: {
      //       "Content-Type": "application/json",
      //     },
      //   }
      // )
      //   .then((res) => {
      //     this.watchList = res.data;
      //   })
    },
    move() {
      if (this.isShow) {
        this.isShow = false;
        window.clearTimeout(this.tempObj.hide);
        this.tempObj.hide = null;
        this.updateHide();
      }
    },
    updateHide() {
      if (!this.tempObj.hide) {
        this.tempObj.hide = setTimeout(() => {
          this.isShow = true;
        }, this.hideTime * 1000);
      }
    },
    //
    statusChange(status) {
      if (status) {
        this.init();
        // this.deviceList.length = 0
        // const res = {
        //   "msg": "",
        //   "code": 0,
        //   "data": {
        //     "createBy": null,
        //     "createTime": null,
        //     "updateBy": null,
        //     "updateTime": null,
        //     "remark": null,
        //     "id": 6,
        //     "equipName": "",//
        //     "equipNo": "2304##002##WSD001",//
        //     "manufacturers": "",//
        //     "laboratoryNo": "2304",//
        //     "checkTime": "20220513",//
        //     "validDate": "20240512",//
        //     "isDetail": "",//
        //     "model": "WE0129",//
        //     "equipType": ""//
        //   }
        // }
        // this.deviceList.push({ name: "", value: res.data.equipName })
        // this.deviceList.push({ name: "", value: res.data.equipNo })
        // this.deviceList.push({ name: "", value: res.data.manufacturers })
        // this.deviceList.push({ name: "", value: res.data.laboratoryNo })
        // this.deviceList.push({ name: "", value: res.data.checkTime })
        // this.deviceList.push({ name: "", value: res.data.validDate })
        // this.deviceList.push({ name: "", value: res.data.isDetail })
        // updateStatus('');
      }
    },
  },
  beforeDestroy() {
    clearInterval(this.interval);
    store.updateStatus('');
    //  localStorage
    // localStorage.clear();
  }
};
</script>
<style scoped>
@import url("../../assets/css/index.css");
</style>
<style scoped>
.indexBox {
  /* padding: 10px; */
}

::-webkit-scrollbar {
  display: none;
}
</style>
